    <MglMap
      class="map"
      :accessToken="accessToken"
      :mapStyle="mapStyle"
      :center="center"
      :zoom.sync="zoom"
      @load="onMapLoaded"
    >
      <MglNavigationControl position="top-left"/>
<!--      <MglPopup :coordinates="[83.214, 60.703]" :showed="showed" :closeButton="false">-->
<!--        Hello!-->
<!--        <v-sheet-->
<!--          rounded-->
<!--          color="white"-->
<!--          elevation="4"-->
<!--          width="100"-->
<!--          style="overflow: hidden">-->
<!--          <v-progress-linear-->
<!--            :value="test"-->
<!--            color="red"-->
<!--            height="25"-->
<!--          ><strong>о - text</strong>-->
<!--          </v-progress-linear>-->
<!--          <v-progress-linear-->
<!--            :value="70"-->
